@charset "UTF-8";
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Roboto Medium"), url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Roboto Condensed"), url("../fonts/roboto_condensed-regular.woff2") format("woff2"), url("../fonts/roboto_condensed-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: local("Roboto Condensed Bold"), url("../fonts/roboto_condensed-bold.woff2") format("woff2"), url("../fonts/roboto_condensed-bold.woff") format("woff"); }

@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("PT Mono"), url("../fonts/pt_mono-regular.woff2") format("woff2"), url("../fonts/pt_mono-regular.woff") format("woff"); }

/*Цвета*/
a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.no-scroll {
  height: 100vh;
  overflow: hidden; }

.page {
  height: 100%;
  scroll-behavior: smooth; }

.page-body {
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0 auto;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  scroll-behavior: smooth; }

.page-header {
  background-color: #1b2d37; }

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  max-width: 1420px;
  margin: 0 auto;
  padding: 32px 10px 23px; }
  @media (max-width: 1023px) {
    .page-header__wrapper {
      max-width: 100%;
      padding: 21px 24px 0; } }
  @media (max-width: 767px) {
    .page-header__wrapper {
      padding: 17px 16px 0; } }

.logo {
  display: flex;
  max-width: 250px; }

.logo__img img {
  display: block; }
  @media (max-width: 767px) {
    .logo__img img {
      width: 38px;
      height: 38px; } }

.logo__text {
  margin-left: 16px; }
  @media (max-width: 767px) {
    .logo__text {
      display: none; } }

.logo__text h2 {
  margin: 0;
  padding-top: 6px;
  font-weight: bold;
  font-size: 25px;
  line-height: 29px; }

.logo__text p {
  margin: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: #788891; }

.user-nav {
  display: flex;
  flex-wrap: wrap;
  align-items: center; }

.user-nav__link {
  display: flex;
  align-items: center;
  margin-left: 38px;
  margin-right: 50px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  color: #ffffff; }
  .user-nav__link:hover {
    color: #00b2ff; }
  .user-nav__link:active {
    opacity: 0.5; }
  @media (max-width: 1023px) {
    .user-nav__link {
      margin: 0; } }

@media (max-width: 1023px) {
  .user-nav__link--mail svg {
    width: 25px;
    height: 19px; } }

@media (max-width: 1023px) {
  .user-nav__link--tel svg {
    width: 24px;
    height: 24px; } }

.user-nav__link:not(:last-of-type) svg {
  margin-right: 20px; }
  @media (max-width: 1023px) {
    .user-nav__link:not(:last-of-type) svg {
      margin-right: 33px; } }
  @media (max-width: 767px) {
    .user-nav__link:not(:last-of-type) svg {
      margin-right: 23px; } }

.user-nav__link:last-of-type svg {
  margin-right: 16px; }
  @media (max-width: 1023px) {
    .user-nav__link:last-of-type svg {
      margin-right: 0; } }

@media (max-width: 1023px) {
  .user-nav__link span {
    display: none; } }

.user-nav__button {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
  padding: 14px 25px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 5px;
  cursor: pointer; }
  .user-nav__button:hover {
    background-color: #00b2ff; }
  .user-nav__button:active {
    opacity: 0.5; }
  @media (min-width: 1024px) and (max-width: 1420px) {
    .user-nav__button {
      margin-right: 0; } }
  @media (max-width: 1023px) {
    .user-nav__button {
      display: none; } }

.promo {
  background-color: #1b2d37;
  background-image: url("../img/main_mask.svg"), url("../img/main_background.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom; }

.promo__wrapper {
  display: flex;
  width: 1580px;
  margin: 0 auto;
  padding-top: 150px; }

.promo__wrapper img {
  display: block;
  overflow: hidden; }

.promo__description {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 720px;
  margin: 0 70px 0 80px; }

.promo__text h2 {
  margin: 0;
  margin-bottom: 30px;
  padding-top: 58px;
  font-size: 80px;
  line-height: 94px;
  font-weight: bold;
  letter-spacing: 0.07em;
  text-transform: uppercase; }

.promo__text p {
  position: relative;
  margin: 0;
  margin-bottom: 44px;
  padding-left: 98px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: normal;
  font-size: 22px;
  line-height: 34px; }
  .promo__text p::before {
    position: absolute;
    content: "";
    top: 14px;
    left: 3px;
    width: 66px;
    height: 2px;
    background-color: #ffffff; }

.promo__button {
  margin-left: 98px;
  margin-bottom: 93px;
  padding: 17px 45px;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #00b2ff;
  border-radius: 5px; }
  .promo__button:hover {
    opacity: 0.7; }
  .promo__button:active {
    opacity: 0.2; }

.promo__scroll {
  margin-bottom: 92px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.5;
  writing-mode: vertical-lr;
  transform: rotate(180deg); }
  .promo__scroll:hover {
    color: #00b2ff; }
  .promo__scroll:active {
    opacity: 0.2; }

.popup {
  position: fixed;
  top: 136px;
  left: 0;
  right: 0;
  width: 490px;
  margin: auto;
  background-color: #00b2ff; }
  .popup--closed {
    display: none; }
  .popup--opened {
    display: block; }

.popup__wrapper {
  padding: 30px 38px 40px; }

.popup__wrapper h2 {
  margin: 0;
  margin-bottom: 12px;
  font-size: 28px;
  line-height: 39px; }

.popup__wrapper p {
  margin: 0;
  margin-bottom: 33px;
  padding-right: 15px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px; }

.popup form {
  display: flex;
  flex-direction: column;
  width: 100%; }

.popup input,
.popup textarea {
  padding: 15px 24px;
  margin-bottom: 30px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  background: rgba(255, 255, 255, 0.1);
  border: none;
  box-sizing: border-box;
  color: #ffffff; }
  .popup input:hover,
  .popup textarea:hover {
    border: 1px solid #ffffff; }
  .popup input:focus,
  .popup textarea:focus {
    border: 2px solid #ffffff; }
  .popup input::placeholder,
  .popup textarea::placeholder {
    font-family: "PT Mono", "Arial", sans-serif;
    font-size: 16px;
    line-height: 22px;
    color: #ffffff; }
  .popup input::-moz-placeholder,
  .popup textarea::-moz-placeholder {
    font-family: "PT Mono", "Arial", sans-serif; }

.popup textarea {
  height: 104px;
  resize: none; }

.popup__agreement {
  display: flex; }

.popup__agreement label {
  position: relative;
  padding-left: 48px;
  padding-right: 30px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.8; }
  .popup__agreement label::before {
    position: absolute;
    content: "";
    left: 0;
    top: 2px;
    width: 16px;
    height: 16px;
    background-color: transparent;
    border: 1px solid #ffffff; }

.popup__agreement input:checked + label::after {
  position: absolute;
  content: "";
  left: 4px;
  top: 5px;
  width: 10px;
  height: 10px;
  background-color: transparent;
  background-image: url("../img/check_icon.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%; }

.popup__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 34px;
  padding: 14px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 5px;
  cursor: pointer; }
  .popup__button:hover {
    background-color: #00b2ff; }
  .popup__button:active {
    opacity: 0.5; }

.popup__button-close {
  position: absolute;
  top: 0;
  right: 0;
  width: 60px;
  height: 60px;
  background-color: transparent;
  border: 0;
  cursor: pointer; }

.popup__button-close::before,
.popup__button-close::after {
  content: "";
  position: absolute;
  top: 30px;
  left: 20px;
  width: 23px;
  height: 1px;
  background-color: #ffffff; }

.popup__button-close::before {
  transform: rotate(45deg); }

.popup__button-close::after {
  transform: rotate(-45deg); }
